import {getEnvironmentDetails} from "../helpers";
import axios from "axios";

export interface IIconFinderClient {
    getImage(url: string): any;
}

export class IconFinderApiClient implements IIconFinderClient {
    token: string;
    constructor() {
        const environment = getEnvironmentDetails();
        this.token = environment.iconFinderApiKey;
    }
    getImage(url: string): any {
        axios.get(url, {
            headers: {
                Authorization: `Bearer ${this.token}`,
                responseType: 'document'
            },
        }).then(data => console.log(data)).catch(err => console.log(err))
    }
}